  background-image: radial-gradient(circle, rgb(63, 237, 251) 0%, rgba(252, 70, 107, 0) 71%), linear-gradient(transparent, transparent), linear-gradient(transparent, transparent), linear-gradient(transparent, transparent);
  animation: borderRotate 4s linear infinite;
  z-index: -1;
  pointer-events: none;
